.toggle {
  cursor: pointer;
  width: 2rem;
  height: 2rem;
}

#flyoutMenu {
  width: 100vw;
  height: 100vh;
  background-color: #23212B;
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  top: 0;
  left: 0;
  -webkit-transition: -webkit-transform .3s
              cubic-bezier(0, .52, 0, 1);
  transition: -webkit-transform .3s
              cubic-bezier(0, .52, 0, 1);
  -o-transition: transform .3s
              cubic-bezier(0, .52, 0, 1);
  transition: transform .3s
              cubic-bezier(0, .52, 0, 1);
  transition: transform .3s
              cubic-bezier(0, .52, 0, 1), -webkit-transform .3s
              cubic-bezier(0, .52, 0, 1);
  overflow: scroll;
  z-index: 1000;
}

#flyoutMenu.hide {
  -webkit-transform: translate3d(-100vw, 0, 0);
          transform: translate3d(-100vw, 0, 0);
}

#flyoutMenu.show {
  -webkit-transform: translate3d(0vw, 0, 0);
          transform: translate3d(0vw, 0, 0);
  overflow: hidden;
}

a {
  color: #fff;
  margin-left: 1rem;
  text-decoration: none;
}

@media screen and (max-width: 40rem) {
  .toggle {
  }
}

.menu-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width: 100%;
  height: 3.75rem;
}

.menu-footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  width: 100%;
  height: 3.75rem;
}

.close-menu {
  width: 2rem;
  height: 2rem;
  margin-left: 1%;
  cursor: pointer;
}

.menu-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 3rem;
  width: 100%;
  margin: 1rem;
}

.ubung {
  background-color: #37B1E3;
}

.prufung {
  background-color: #DD5152;
}

.alleFragen {
  background-color: #2CC990;
}



